                this.indexCaract = b;
                this.tempDienteSelected = this.dienteSelected;
                this.msg = " Seleccione 2do Diente"

            }, deAfueraSimple: function (carac, index) {

                let query = "#liDiente" + this.dienteSelected.numero + " g title";
                let element = [...document.querySelectorAll(query)].find(x => x.textContent === carac).parentElement;
                if (element.getAttribute("display") === "none") {
