<template>
    <div style="text-align: left;margin: 1em;border: 1px solid lightgray;" id="panelCaract">
        <div class="divCaractPuente"
            style="text-align: center;display: inline-block;border: 2px solid lightgray;margin: .25em; padding: .2em;cursor: pointer"
             v-bind:class="{  'selectedClass' : (caractSelected === caract && !deshabilitarCancelar) }"
            v-for="(caract, index) in listaCaract"
            @click="setSelected(caract, index)">

            <img
                :src="getImgUrl(caract.nombreArchivo)"
                :style="'display: block;border: 0px solid red;margin: 0 auto'"
                alt="">
            {{caract.nombreVisible}}
        </div>


        <div
            style="margin: .25em; padding: .2em"
        >
            <div v-show="!deshabilitarCancelar === true">
                Seleccione un segundo diente para dibujar <span  style="font-weight: bold">{{ caractSelected.nombreVisible}}</span>
            </div>
            <div v-show="deshabilitarCancelar === true">
                &nbsp;
            </div>
            <button @click="cancelarPuente" :disabled="deshabilitarCancelar === true">Cancelar</button>
        </div>

    </div>
</template>
<script>
    /* eslint-disable */
    //    import listaItems from './../js/data12-2.js';
    export default {
        name: "PanelCaractPuentes",
        props: {
            model: Object,
            deshabilitarCancelar: Boolean
        },
        data: function () {
            return {
                listaCaract: [
                    {nombreArchivo: 'ortofijo', nombreVisible: 'Aparato Ortodóntico Fijo'},
                    {nombreArchivo: 'ortoremo', nombreVisible: 'Aparato Ortodóntico Removible'},
                    {nombreArchivo: 'protefija', nombreVisible: 'Prótesis Fija'},
                    {nombreArchivo: 'proteremo', nombreVisible: 'Prótesis Removible'},
                    {nombreArchivo: 'edentulototal', nombreVisible: 'Edéntulo Total'},
//                    {nombreArchivo: 'protetotal', nombreVisible: 'Prótesis Totales'},
                ],
                caractSelected: {},
                indexSelected: -1
            }
        },
        computed: {
            isFolder: function () {
            }
        },
        methods: {
            getImgUrl(nombre) {
                let images = require.context('../components/CaractPuentes/', false, /\.svg$/)
                return images('./' + nombre + ".svg")
            },
            cancelarPuente() {
                this.caractSelected = {};
                this.$emit('cancelarPuente')
            },
            setSelected: function (caract, index) {


                this.indexSelected = index;
                this.caractSelected = caract;
                this.$emit('caract', caract.nombreVisible, index)
            }

        },
        mounted(){

        }
    }
</script>

