/* eslint-disable */

let helper = {
    generarPuntos: function (listaDientes,inicioFin) {

        let listaElementos = [];
        listaDientes.forEach(diente => {
            if (inicioFin.ini <= diente.numero && diente.numero <= inicioFin.fin) {
                listaElementos.push(document.getElementById("liDiente" + diente.numero));
            }
        });

        let puntoInicioX = listaElementos[0].offsetLeft ;
        let puntoInicioY = listaElementos[0].offsetTop - 10;
        let listaPuntosZ = [];

        listaPuntosZ.push({x:puntoInicioX,y:puntoInicioY});

        // listaElementos.forEach(e => {
        //     let punto1x,punto1y,punto2x,punto2y;
        //     punto1x = e.offsetLeft + e.offsetWidth/2;
        //     punto1y = e.offsetTop-30;
        //     punto2x = e.offsetLeft + e.offsetWidth;
        //     punto2y = e.offsetTop- 10;
        //
        //     listaPuntosZ.push({x:punto1x,y:punto1y});
        //     listaPuntosZ.push({x:punto2x,y:punto2y});
        // });

        listaElementos.forEach(e => {
            let punto1x = e.offsetLeft + e.offsetWidth/4;
            let punto1y = e.offsetTop-30;
            let punto2x = e.offsetLeft + e.offsetWidth/2;
            let punto2y = e.offsetTop- 10;

            let punto3x = e.offsetLeft + e.offsetWidth/4*3;
            let punto3y = e.offsetTop-30;
            let punto4x = e.offsetLeft + e.offsetWidth;
            let punto4y = e.offsetTop- 10;

            listaPuntosZ.push({x:punto1x,y:punto1y});
            listaPuntosZ.push({x:punto2x,y:punto2y});
            listaPuntosZ.push({x:punto3x,y:punto3y});
            listaPuntosZ.push({x:punto4x,y:punto4y});

        });




        return listaPuntosZ;

    }
};

export default helper;
